@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap');

* {
  color: #eeeeee;
  font-family: 'Roboto'
}

body {
  background: #264653;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.app {
  background-color: #2a9d8f;
  width: 450px;
  min-height: 200px;
  border-radius: 15px;
  padding: 20px;
  display: flex;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
}

.quizz {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.question_section {
  flex: 0 0 50%;
}

.question_count {
  margin-bottom: 20px;
}

.question_count span {
  font-size: 28px;
}

.question_text {
  margin-bottom: 15px;
}

.answer_section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

