button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e76f51;
  color: #eeeeee;
  font-size: 20px;
  text-align: center;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px 0;
}
button span {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: 0.5s;
}
button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
button:hover {
  background-color: #f4a261;
}
button:hover span {
  padding-right: 25px;
}
button:hover span:after {
  opacity: 1;
  right: 0;
}

.section_score {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 24px;
  width: 100%;
}

@media (max-width: 500px) {
  .app {
    background-color: #2a9d8f;
    width: 300px;
    min-height: 300px;
  }
  button {
    font-size: 15px;
  }
  .answer_section {
    justify-content: center;
  }
  .quizz {
    justify-content: end;
  }
  .question_section {
    flex: 0 0 0;
  }
  .question_text {
    margin-right: 15px;
  }
}